@import "https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap";
*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Ubuntu, sans-serif;
}

.container {
  color: #fff;
  background-color: #000;
  width: 100%;
  height: 100dvh;
  position: relative;
}

.info-block {
  background-color: #2a2a2a;
  border-radius: 10px;
  flex-direction: column;
  row-gap: 10px;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 20px;
  left: 20px;

  & > h1 {
    border-bottom: 1px solid #ffffff80;
    padding-bottom: 20px;
    font-size: 25px;
  }
}

.input-group {
  align-items: center;
  column-gap: 10px;
  display: flex;
}

.app-points-count-input {
  color: #fff;
  background-color: #ffffff1a;
  border: none;
  border-radius: 5px;
  outline: none;
  max-width: 150px;
  padding: 5px 10px;
  font-size: 25px;
  transition: background-color .2s;

  &:hover {
    background-color: #ffffff59;
  }
}

.app-fps-output {
  color: #fff;
  font-family: sans-serif;
  font-size: 25px;
  font-weight: 100;

  &:after {
    color: #fff;
    content: " fps";
  }
}

.app-canvas {
  width: 100%;
  height: 100%;
}
